import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {Router} from '@angular/router';
import {AngularFireAuth} from '@angular/fire/auth';
import {User} from '../../models/user/user.model';
import {Camera, CameraResultType, CameraSource} from '@capacitor/core';
import {UserRoles} from '../../models/user/user-roles.enum';
import {AngularFireStorage, AngularFireUploadTask} from '@angular/fire/storage';
import {ModalController} from '@ionic/angular';
  import {TerminosView} from '../../views/terminos/terminos.component';
import {ProteccionView} from '../../views/proteccion/proteccion.component';

@Component({
  selector: 'app-register-user',
  templateUrl: './register-user.component.html',
  styleUrls: ['./register-user.component.sass']
})
export class RegisterUserComponent implements OnInit {
  private userProfileCollection: AngularFirestoreCollection;
  public registrationForm: FormGroup;
  public profilePicture: string;

  constructor(private formBuilder: FormBuilder, public firestore: AngularFirestore, private router: Router,
              public fireauth: AngularFireAuth, private storage: AngularFireStorage, public modalCtrl: ModalController) {
    this.userProfileCollection = firestore.collection('profiles');
  }

  presentModal(modalToDisplay: number) {
    if (modalToDisplay == 0) {
      this.modalCtrl.create({
        component: TerminosView
      }).then(value => {
        value.present();
      });
    } else {
      this.modalCtrl.create({
        component: ProteccionView
      }).then(value => {
        value.present();
      });
    }

  }

  ngOnInit(): void {
    this.registrationForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      name: ['', [Validators.required]],
      password: ['', [Validators.required]],
      phoneNumber: ['', []],
      picture: ['', ],
      location: ['', ],
      isFarmer: [false, []],
      newsSub: [false, []],
    });
  }

  registerUser(): void {
    if (this.registrationForm.valid) {
      const userToCreate: User = new User();
      userToCreate.name = this.registrationForm.value.name;
      userToCreate.email = this.registrationForm.value.email;
      userToCreate.location = this.registrationForm.value.location;
      userToCreate.phoneNumber = this.registrationForm.value.phoneNumber;
      userToCreate.role = this.registrationForm.value.isFarmer ? UserRoles.PRODUCER : UserRoles.USER;
      userToCreate.newsSub = this.registrationForm.value.newsSub;

      this.fireauth.createUserWithEmailAndPassword(userToCreate.email, this.registrationForm.value.password)
        .then((user) => {
          if (this.profilePicture) {
            this.uploadPictureToProfile(this.profilePicture, user.user.uid).then(
              a => {
                userToCreate.picture = this.registrationForm.value.picture;
                user.user.updateProfile({photoURL: userToCreate.picture});
                this.userProfileCollection.doc(user.user.uid)
                  .set(userToCreate.convertToDTO())
                  .catch(error => {
                    console.log('Something went wrong with added user to firestore: ', error);
                  }).then(value => this.changeView(user.user.uid));
              }
            );
          } else {
            userToCreate.picture = '/userProfilePics/default/favicon.png';
            this.userProfileCollection.doc(user.user.uid)
              .set(userToCreate.convertToDTO())
              .catch(error => {
                console.log('Something went wrong with added user to firestore: ', error);
              }).then(value => this.changeView(user.user.uid));
          }
        });
    } else {
      this.registrationForm.markAllAsTouched();
    }
  }

  changeView(uid: string): void {
    this.router.navigate(['profile']);
  }

  uploadPictureToProfile(picture: any, userID: string): AngularFireUploadTask {
    const file = picture;
    const filePath = '/userProfilePics/' + userID + '/' + 'myPicture.png';
    this.registrationForm.value.picture = filePath;
    const ref = this.storage.ref(filePath);
    return ref.putString(file, 'base64');
  }

  async takePicture() {
    await Camera.getPhoto({
      quality: 50,
      allowEditing: true,
      resultType: CameraResultType.Base64,
      webUseInput: true,
      source: CameraSource.Prompt
    }).then(value => {
      this.registrationForm.value.picture = 'Foto cargada';
      this.profilePicture = value.base64String;
      });
  }
}
