import {Component, Input, OnInit, Output, EventEmitter} from '@angular/core';
import {Order, OrderStatusEnum} from '../../models/order/order';
import {AuthServiceService} from '../../services/auth/auth-service.service';
import {ProfilesService} from '../../services/profiles/profiles.service';
import {User} from '../../models/user/user.model';
import {OrderService} from '../../services/order/order.service';

@Component({
  selector: 'app-order-item',
  templateUrl: './order-item.component.html',
  styleUrls: ['./order-item.component.scss']
})
export class OrderItemComponent implements OnInit {
  @Input() order: Order;
  @Output() refreshViewEvent = new EventEmitter<string>();

  public finalPrice = 0;
  private isFarmer = false;
  private orderUser: User;

  constructor(public authService: AuthServiceService, public profileService: ProfilesService, public orderService: OrderService) {
    this.isFarmer = authService.getIsFarmer();
    this.orderUser = new User();
  }

  ngOnInit(): void {
    this.finalPrice = this.order.getOrderPrice();
    this.profileService.loadUserById(this.order.userId).then(value => {
      this.orderUser = value;
    });
  }

  cancelOrder() {
    this.orderService.cancelOrderById(this.order.id).then(value => {
      if (value) {
        this.order.status = OrderStatusEnum.Cancelado;
        this.refreshViewEvent.emit(value);
      }
    });
  }

  prepareOrder() {
    this.orderService.markOrderAsPreparedById(this.order.id).then(value => {
      if (value) {
        this.order.status = OrderStatusEnum.Preparado;
        this.refreshViewEvent.emit(value);
      }
    });
  }

  deliveredOrder() {
    this.orderService.markOrderAsDeliveredById(this.order.id).then(value => {
      if (value) {
        this.order.status = OrderStatusEnum.Preparado;
        this.refreshViewEvent.emit(value);
      }
    });
  }
}
