export class Stats {
  totalSaleAmount: number;
  totalOrders: number;
  productsOrdered: StatsProductOrders[];

  constructor() {
    this.productsOrdered = [];
    this.totalOrders = 0;
    this.totalSaleAmount = 0;
  }
}

export class StatsProductOrders {
  productId: string;
  productAmount: number;
  numberOfOrders: number;
  name: string;

  constructor() {
    this.productAmount = 0;
    this.numberOfOrders = 0;
    this.name = '';
  }
}
