import {Component, Input, OnInit} from '@angular/core';
import {Product, UnitTypeEnum} from '../../models/product/product';
import {ActivatedRoute, Router} from '@angular/router';
import {ProductService} from '../../services/products/product.service';
import {AuthServiceService} from '../../services/auth/auth-service.service';
import {EnumToArrayPipe} from '../../pipes/enum-to-array.pipe';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss'],
})
export class ProductListComponent implements OnInit {
  @Input() product: Product;
  private unitsList: string[];

  constructor(private router: Router, private route: ActivatedRoute, public productService: ProductService,
              private authService: AuthServiceService, public enumToArrayPipe: EnumToArrayPipe) {
    this.unitsList = this.enumToArrayPipe.transform(UnitTypeEnum);
  }

  ngOnInit() {}

  openProduct(product: Product) {
    if (product.stock || product.ownerId === this.authService.getCurrentUserId()) {
      this.router.navigate(['/product', product.id]);
    }
  }
}
