import { Component, OnInit } from '@angular/core';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {Product, UnitTypeEnum} from '../../models/product/product';
import {AngularFireStorage} from '@angular/fire/storage';
import {ActivatedRoute, Router} from '@angular/router';
import {ProductService} from '../../services/products/product.service';
import {EnumToArrayPipe} from '../../pipes/enum-to-array.pipe';
import {AuthServiceService} from '../../services/auth/auth-service.service';
import {ViewWillEnter} from '@ionic/angular';
import { ViewChild } from '@angular/core';



@Component({
  selector: 'app-products',
  templateUrl: './products.view.html',
  styleUrls: ['./products.view.scss']
})
export class ProductsView implements OnInit, ViewWillEnter {
  // @ViewChild('mySearchbar') searchbar: Searchbar;

  public filteredProducts: Product[];
  public productList: Product[];
  private pathId: string;
  private isSearch = false;
  searchTerms: any;

  constructor(public firestore: AngularFirestore, public storage: AngularFireStorage,
              private router: Router, private route: ActivatedRoute, public productService: ProductService,
              private authService: AuthServiceService) {
    this.productList = [];
    this.filteredProducts = [];
    this.pathId = null;
  }

  ionViewWillEnter() {
    this.route.params.subscribe( params => {
      if (params.userId) {
        this.pathId = params.userId;
        this.productList = [];
        this.filteredProducts = [];
        this.isSearch = true;
        this.productService.loadProductsByOwnerId(this.pathId).then(value => {
          this.productList = value;
          this.filteredProducts = this.productList;
          this.filteredProducts.sort(compare);
        });
      } else {
        this.pathId = null;
        this.isSearch = false;
        this.productService.loadAllProducts().then(value => {
          this.productList = value;
          this.filteredProducts = this.productList;
          this.filteredProducts.sort(compare);
        });
      }
    });
  }

  ngOnInit(): void {
  }

  openProduct(product: Product) {
    if (product.stock || product.ownerId === this.authService.getCurrentUserId()) {
      this.router.navigate(['/product', product.id]);
    }
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.filteredProducts = this.productList;
    // set val to the value of the searchbar
    const val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() !== '') {
      this.isSearch = true;
      this.filteredProducts = this.productList.filter(value => {
        return (value.name.toLowerCase().indexOf(val.toLowerCase()) > -1);
      });
      // this.items = this.items.filter((item) => {
      //  return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
      //  });
    } else {
      if (!this.pathId) {
        this.isSearch = false;
      }
    }
  }

  searchCategory(idNumber: number) {
    this.isSearch = true;
    this.filteredProducts = this.productList.filter(value => {
      // @ts-ignore
      return value.category === idNumber;
    });
  }

  cancelSearch() {
    this.filteredProducts = this.productList;
    if (!this.pathId) {
      this.isSearch = false;
    }
  }
}

function compare(a, b) {
  if (a.stock && !b.stock){
    return -1;
  }
  if (!a.stock && b.stock){
    return 1;
  }
  return 0;
}
