import {Injectable, OnDestroy} from '@angular/core';
import {AngularFireAuth} from '@angular/fire/auth';
import {Router} from '@angular/router';
import {AngularFirestore, AngularFirestoreDocument} from '@angular/fire/firestore';
import {User} from '../../models/user/user.model';
import {UserRoles} from '../../models/user/user-roles.enum';

@Injectable({
  providedIn: 'root'
})
export class AuthServiceService implements OnDestroy{
  private isLogged: boolean;
  private userCollection: AngularFirestoreDocument<unknown>;
  private isFarmer: boolean;
  private userId: string;

  constructor(public afAuth: AngularFireAuth, public router: Router, public firestore: AngularFirestore) {
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userId = user.uid;
        this.isLogged = true;
        this.userCollection = firestore.doc('profiles/' + user.uid);
        this.userCollection.valueChanges().subscribe(userData => {
          console.log(userData);
          const realUser = new User().convertFromDTO(userData);
          this.isFarmer = realUser.role === UserRoles.PRODUCER;
        });
      } else {
        this.isLogged = false;
      }
    });
  }

  getCurrentUserId(){
    return this.userId ? this.userId : 'test';
  }

  getIsFarmer(){
    return this.isFarmer;
  }

  ngOnDestroy(): void {
  }
}
