import { Component, OnInit } from '@angular/core';
import {User} from '../../models/user/user.model';
import {ActivatedRoute, Router} from '@angular/router';
import {AngularFirestore, AngularFirestoreDocument} from '@angular/fire/firestore';
import {AngularFireAuth} from '@angular/fire/auth';
import {AngularFireStorage} from '@angular/fire/storage';
import {UserRoles} from '../../models/user/user-roles.enum';
import {TerminosView} from '../terminos/terminos.component';
import {ModalController} from '@ionic/angular';
import {DateModalComponent} from '../../components/date-modal/date-modal.component';
import { DatePipe } from '@angular/common';
import {ProfilesService} from '../../services/profiles/profiles.service';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.view.html',
  styleUrls: ['./profile.view.sass']
})
export class ProfileView implements OnInit {
  public user: User;
  public pathId: string;
  private loggedId: string;
  private userCollection: AngularFirestoreDocument<User>;
  public profileImage: any;
  public isFarmer = false;
  public currentPickUpDate: Date;

  public today: string;

  constructor(private router: Router, private route: ActivatedRoute,
              public firestore: AngularFirestore, public afAuth: AngularFireAuth,
              public storage: AngularFireStorage, public window: Window,
              public modalCtrl: ModalController, public profileService: ProfilesService) {
    this.user = new User();

    const datepipe: DatePipe = new DatePipe('en-US');
    this.today = datepipe.transform(new Date(), 'YYYY-MM-dd');

    this.currentPickUpDate = new Date();

    console.log(this.today);

    this.route.params.subscribe( params => {
      if (params.id) {
        this.pathId = params.id;
        this.userCollection = firestore.doc<User>('profiles/' + this.pathId);
        this.userCollection.valueChanges({idField: '_id'}).subscribe(user => {
          this.user = this.user.convertFromDTO(user);
          if (this.user.pickupDate) {
            this.currentPickUpDate = this.user.pickupDate;
          }
          this.isFarmer = this.user.role === UserRoles.PRODUCER;
          const ref = this.storage.ref(this.user.picture);
          ref.getDownloadURL().subscribe(url => {
            this.profileImage = url;
          });
        });
      } else {
        this.afAuth.authState.subscribe(loggedUser => {
          if (loggedUser) {
            this.loggedId = loggedUser.uid;
            this.pathId = loggedUser.uid;
            this.userCollection = firestore.doc<User>('profiles/' + this.pathId);
            this.userCollection.valueChanges({idField: '_id'}).subscribe(user => {
              this.user = this.user.convertFromDTO(user);
              this.isFarmer = this.user.role === UserRoles.PRODUCER;
              const ref = this.storage.ref(this.user.picture);
              ref.getDownloadURL().subscribe(url => {
                this.profileImage = url;
              });
            });
          }
        });
      }
    });
  }

  ngOnInit(): void {
  }

  logout(){
    this.afAuth.signOut().then(() => {
      this.router.navigate(['/login']);
    });
  }

  changeDate($event: any) {
    this.profileService.updatePickDate(this.user.id, new Date($event.detail.value));
  }
}
