import { Component, OnInit } from '@angular/core';
import {User} from '../../models/user/user.model';
import {UserRoles} from '../../models/user/user-roles.enum';
import {ActivatedRoute, Router} from '@angular/router';
import {OrderService} from '../../services/order/order.service';
import {Stats} from '../../models/statistics/stats';
import {ViewWillEnter} from '@ionic/angular';

@Component({
  selector: 'app-statistics',
  templateUrl: './statistics.view.html',
  styleUrls: ['./statistics.view.scss']
})
export class StatisticsView implements OnInit, ViewWillEnter {
  public anything: Stats;
  constructor(private router: Router, private route: ActivatedRoute, public orderService: OrderService) {
    this.anything = new Stats();
  }

  ngOnInit(): void {
  }

  ionViewWillEnter(){
    this.orderService.loadUserSalesRecord().then(value => this.anything = value);
  }

}
