import { Component, OnInit } from '@angular/core';
import {CartServiceService} from '../../services/order/cart-service.service';
import {ProductService} from '../../services/products/product.service';
import {AlertController} from '@ionic/angular';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.view.html',
  styleUrls: ['./cart.view.scss']
})
export class CartView implements OnInit {
  private cart = [];

  constructor(public cartService: CartServiceService, public  productService: ProductService, public alertController: AlertController) { }

  ngOnInit(): void {
  }

  ionViewDidEnter() {
    this.loadItemsOfCart();
  }

  cartItemEvent(event) {
    if (event.operation === 'delete'){
      this.loadItemsOfCart();
    }
  }

  loadItemsOfCart(){
    this.cart = Array.prototype.concat.apply([], this.cartService.getAllProductOrders());;
  }

  checkout() {
    this.cartService.checkoutCart();
    this.cart = [];
    this.presentAlert();
  }

  async presentAlert() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Enhorabuena',
      message: 'Tu pedido ha sido generado con exito, dirigete a tus pedidos para revisarlo.',
      buttons: ['Aceptar']
    });

    await alert.present();
  }
}
