import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import {LoginView} from '../views/login/login.view';
import {ProfileView} from '../views/profile/profile.view';
import {ProductFormComponent} from '../views/product-form/product-form.component';
import {ProductView} from '../views/product/product.view';
import {ProductsView} from '../views/product-list/products.view';
import {ProfileListView} from '../views/profile-list/profile-list.view';
import {CartView} from '../views/cart/cart.view';
import {OrderListView} from '../views/order-list/order-list.view';
import {StatisticsView} from '../views/statistics/statistics.view';
import {ProteccionView} from '../views/proteccion/proteccion.component';
import {TerminosView} from '../views/terminos/terminos.component';

const routes: Routes = [
  {path: '', redirectTo: '/login', pathMatch: 'full'},
  {path: 'login', component: LoginView},
  {path: 'profile', component: ProfileView},
  {path: 'myProfile', component: ProfileView},
  {path: 'profile/:id', component: ProfileView},
  {path: 'profiles', component: ProfileListView},
  {path: 'profiles/:isFarmer', component: ProfileListView},
  {path: 'productForm', component: ProductFormComponent},
  {path: 'productForm/:id', component: ProductFormComponent},
  {path: 'products', component: ProductsView},
  {path: 'products/:category', component: ProductsView},
  {path: 'products/:category/:userId', component: ProductsView},
  {path: 'products/:category/:userId/:searchText', component: ProductsView},
  {path: 'profileProducts/:category', component: ProductsView},
  {path: 'profileProducts/:category/:userId', component: ProductsView},
  {path: 'product/:id', component: ProductView},
  {path: 'cart', component: CartView},
  {path: 'orders', component: OrderListView},
  {path: 'statistics', component: StatisticsView},
  {path: 'politicas', component: ProteccionView},
  {path: 'condiciones', component: TerminosView},
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
