import {LOCALE_ID, NgModule} from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';

import { IonicModule, IonicRouteStrategy, ModalController } from '@ionic/angular';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import {AngularFireModule} from '@angular/fire';
import {environment} from '../environments/environment.prod';
import {AngularFireAnalyticsModule} from '@angular/fire/analytics';
import {AngularFirestoreModule} from '@angular/fire/firestore';
import {CommonModule, HashLocationStrategy, LocationStrategy, registerLocaleData} from '@angular/common';
import {LoginUserComponent} from '../components/login-user/login-user.component';
import {LoginView} from '../views/login/login.view';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {RegisterUserComponent} from '../components/register-user/register-user.component';
import {NavigationBarComponent} from '../components/navigation-bar/navigation-bar.component';
import {AngularFireStorageModule} from '@angular/fire/storage';
import {ProfileView} from '../views/profile/profile.view';
import {ProductFormComponent} from '../views/product-form/product-form.component';
import {EnumToArrayPipe} from '../pipes/enum-to-array.pipe';
import {ProductView} from '../views/product/product.view';
import {ProductsView} from '../views/product-list/products.view';
import {ProfileListView} from '../views/profile-list/profile-list.view';
import {CartView} from '../views/cart/cart.view';
import {CartItemComponent} from '../components/cart-item/cart-item.component';
import {OrderListView} from '../views/order-list/order-list.view';
import {OrderItemComponent} from '../components/order-item/order-item.component';
import {StatisticsView} from '../views/statistics/statistics.view';
import {ProductListComponent} from '../components/product-list/product-list.component';
import {TerminosView} from '../views/terminos/terminos.component';
import {ProteccionView} from '../views/proteccion/proteccion.component';
import {DateModalComponent} from '../components/date-modal/date-modal.component';

import localeEs from '@angular/common/locales/es';

@NgModule({
    declarations: [AppComponent,
        /*  VIEWS */
        LoginView,
        ProfileView,
        ProductView,
        ProductsView,
        CartView,
        OrderListView,
        StatisticsView,
        TerminosView,
        ProteccionView,

        /* COMPONENTS */
        RegisterUserComponent,
        NavigationBarComponent,
        ProductFormComponent,
        ProfileListView,
        DateModalComponent,
        LoginUserComponent, CartItemComponent, OrderItemComponent, ProductListComponent],
  entryComponents: [],
  imports: [CommonModule,
    BrowserModule,
    IonicModule.forRoot(),
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    AngularFireModule.initializeApp(environment.firebaseConfig),
    AngularFirestoreModule,
    AngularFireAnalyticsModule,
    AngularFireStorageModule],
  providers: [{ provide: LocationStrategy, useClass: HashLocationStrategy },
    EnumToArrayPipe, ModalController,
    { provide: LOCALE_ID, useValue: 'es' },
    { provide: Window, useValue: window }],
  bootstrap: [AppComponent],
})
export class AppModule {
}
registerLocaleData(localeEs, 'es');
